 .custom_checkbox input[type="checkbox"]{
   display: none;
 }
 .custom_checkbox input[type="checkbox"] + label{
   position: relative;
   display: inline-block;
   padding-left: 2em;
   cursor: pointer;
   height: 25px;
   line-height: 1.8em;
   -webkit-transition: all 0.3s ease-in-out;
   transition: all 0.3s ease-in-out;
 }
 .custom_checkbox input[type="checkbox"] + label:before,
 .custom_checkbox input[type="checkbox"] + label:after{
   content: '';
   position: absolute;
   top: 0;
   left: 0;
   width: 25px;
   height: 25px;
   text-align: center;
   color: white;
   font-family: Times;
   border-radius: 50%;
   -webkit-transition: all .3s ease;
   transition: all .3s ease;
 }
 .custom_checkbox input[type="checkbox"] + label:before {
   -webkit-transition: all .3s ease;
   transition: all .3s ease;
   box-shadow: inset 0 0 0 1em #CCD4D8;
 }
 .custom_checkbox input[type="checkbox"] + label:hover:before {
   -webkit-transition: all .3s ease;
   transition: all .3s ease;
   box-shadow: inset 0 0 0 0.3em white, inset 0 0 0 1em #c6c6c6;
 }
 .custom_checkbox input[type="checkbox"]:checked + label:before {
   -webkit-transition: all .3s ease;
   transition: all .3s ease;
   box-shadow: inset 0 0 0 1em #7972F3;
 }
 
 .custom_checkbox input[type="checkbox"]:checked + label:after {
     content: '';
     position: absolute;
     left: 7px;
     top: 11px;
     background: white;
     width: 3px;
     height: 3px;
     border-radius: 6px;
     box-shadow: 
     2px 0 0 white,
     4px 0 0 white,
     4px -2px 0 white,
     4px -4px 0 white,
     4px -6px 0 white,
     4px -8px 0 white;
     transform: rotate(45deg);
 }